html {
    .loader {
        background: none repeat scroll 0 0 #ffffff;
        bottom: 0;
        height: 100%;
        left: 0;
        position: fixed;
        right: 0;
        top: 0;
        width: 100%;
        z-index: 9999;
        .loader_inner {
            background-image: url("../images/preloader.gif");
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
            background-color: #fff;
            height: 60px;
            width: 60px;
            margin-top: -30px;
            margin-left: -30px;
            left: 50%;
            top: 50%;
            position: absolute;
        }
    }
    height: 100%;
}

body {
    font-family: 'PT Sans', sans-serif !important;
    font-weight: normal!important;
    font-size: 14px!important;
    min-width: 320px!important;
    position: relative;
    line-height: 1.6;
    -webkit-font-smoothing: antialiased;
    overflow-x: hidden;
    height: 100%;
    input {
        &:focus {
            &:required {
                &:invalid {
                    color: red;
                }
            }
        }
        &:required {
            &:valid {
                color: green;
            }
        }
    }
    textarea {
        &:focus {
            &:required {
                &:invalid {
                    color: red;
                }
            }
        }
        &:required {
            &:valid {
                color: green;
            }
        }
    }
}

.hidden {
    display: none;
}

.not-scroll {
    overflow-y: hidden;
}

button {
    &:active {
        outline: none !important;
    }
    &:focus {
        outline: none !important;
    }
    &::-moz-focus-inner {
