/* колонки одинаковой высоты */

/* flex */

/* no flex */

.row-flex {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    flex-wrap: wrap;
    > div {
        box-sizing: border-box;
        margin: -.3px;
    }
}

.no-flexbox {
    .row-flex {
        display: block;
        > div {
            display: inline-block;
            float: none;
            box-sizing: border-box;
            vertical-align: top;
            margin: 0 -2.5px auto;
        }
    }
}

.clearfix:after {
    content: "";
    display: table;
    clear: both;
}

.mobile-hide {
    max-height: 100px!important;
    opacity: 1!important;
}

.default-btn {
    width: 170px;
    height: 32px;
    display: block;
    line-height: 29px;
    background-color: #ffc107;
    color: #673ab7;
    text-align: center;
    border-radius: 5px;
    font-size: 16px;
    text-decoration: none;
    box-sizing: border-box!important;
    border: 1px solid transparent;
    &:hover {
        text-decoration: none;
        border: 1px solid #673ab7;
        color: #673ab7!important;
    }
    &:focus {
        text-decoration: none;
        border: 1px solid #673ab7;
        color: #673ab7!important;
    }
}

.title {
