}

.on-profile {
    position: relative;
    &:hover {
        &:after {
            color: #ff4d00;
        }
    }
    &:after {
        content: '\e313';
        font-family: 'Material Icons';
        font-size: 16px;
        color: #9575cd;
        position: absolute;
    }
}

.like {
    &:after {
        content: '\e87d';
        position: absolute;
        display: block;
        font-family: 'Material Icons';
        top: 0px;
        right: 0px;
        color: #ff4d00;
        font-size: 24px;
    }
}

.notlike {
    &:after {
        content: '\e87e';
        position: absolute;
        display: block;
        font-family: 'Material Icons';
        top: 0px;
        right: 0px;
        color: #ff4d00;
        color: #aaa2b7;
        font-size: 24px;
    }
}
