/*Sidebar styles*/

.sidebar {
    width: 100%;
    background-color: white;
    border: 1px solid #ddd8e6;
    border-radius: 5px;
    box-shadow: 0px 1px 3px 0px rgba(93, 64, 143, 0.3);
    padding: 25px 10px;
    margin-top: 35px;
    .title {
        font-size: 20px;
        color: #7e57c2;
        margin: 0 0 18px 10px;
        display: block;
    }
}

.sideb-section {
    display: block;
    list-style: none;
    padding: 0 0 30px 0;
    border-bottom: 1px solid #ddd8e6;
    margin-bottom: 25px;
    &:last-child {
        padding: 0;
        border-bottom: none;
    }
    a {
        width: 100%;
        padding: 8.5px 0 8.5px 10px;
        font-size: 16px;
        color: #544370;
        display: block;
        &:before {
            content: '\e315';
            font-family: 'Material Icons';
            font-weight: normal;
            font-style: normal;
            font-size: 15px;
            color: #9575cd;
            margin-right: 10px;
            vertical-align: middle;
        }
        .active {
            background-color: #f7f5fa;
            color: #ff4d00;
            text-decoration: none;
        }
        &:hover {
            background-color: #f7f5fa;
            color: #ff4d00;
            text-decoration: none;
            &:before {
                color: #ff4d00;
            }
        }
        &:focus {
            background-color: #f7f5fa;
            color: #ff4d00;
            text-decoration: none;
            &:before {
                color: #ff4d00;
            }
        }
    }
}

.sideb-filter {
    display: block;
    width: 100%;
}

.clear-filter {
    .reset-btn {
        margin: 0 auto 18px auto;
    }
}

.reset-btn {
    width: 180px;
    height: 32px;
    line-height: 28px;
    display: block;
    text-align: center;
    font-size: 14px;
    background-color: #ebe4f5;
    color: #7e57c2;
    border: none;
    border-radius: 5px;
    &:hover {
        background-color: white;
        border: 1px solid #7e57c2;
    }
}

.clear-filter-list {
    list-style: none;
    padding: 0 0 30px 10px;
    border-bottom: 1px solid #ddd8e6;
    margin-bottom: 25px;
    li {
        font-size: 14px;
        color: #403c4d;
        display: block;
        margin: 0 0 15px 0;
        &:last-child {
            margin: 0;
        }
        a {
            margin-right: 5px;
            color: #7e57c2;
            &:hover {
                color: #ff4d00;
            }
            i {
                font-size: 16px;
                vertical-align: text-bottom;
            }
        }
    }
}

.checkbox-filter {
    list-style: none;
    padding: 0 0 30px 10px;
    border-bottom: 1px solid #ddd8e6;
    margin-bottom: 25px;
    .checkbox:not(checked) {
        + label {
            padding: 0 0 0 30px;
            margin: 0 0 15px 0;
            font-size: 16px;
            color: #544370;
            &:before {
                top: 0px;
            }
        }
    }
    .checkbox {
        &:checked {
            + label {
                color: #ff4d00;
                &:after {
                    top: 0px;
                }
            }
        }
    }
}
.range-filter {
    padding: 0 10px;
    .price-label {
        margin: 10px 0 14px 0;
        font-size: 20px;
        color: #9575cd;
        font-weight: normal;
    }
    .price-range {
        border: none;
        width: 222px;
        height: 40px;
        display: block;
        margin: 0 auto;
        background-color: #ebe4f5;
        color: #544370;
        border-radius: 5px;
        text-align: center;
        font-size: 16px;
    }
    .ui-widget {
        font-size: 0.6em;
        margin-bottom: 29px;
    }
    .ui-widget.ui-widget-content {
        border: 1px solid #ddd8e6;
        background-color: #f4f0fa;
    }
    .ui-widget-header {
        border: 1px solid #9575cd !important;
        background: #9575cd;
        font-weight: bold;
    }
    .ui-corner-all, .ui-corner-bottom, .ui-corner-right, .ui-corner-br {
        border-bottom-right-radius: 10px;
    }
    .ui-corner-all, .ui-corner-bottom, .ui-corner-left, .ui-corner-bl {
        border-bottom-left-radius: 10px;
    }
    .ui-corner-all, .ui-corner-top, .ui-corner-right, .ui-corner-tr {
        border-top-right-radius: 10px;
    }
    .ui-corner-all, .ui-corner-top, .ui-corner-left, .ui-corner-tl {
        border-top-left-radius: 10px;
    }
    .ui-state-default, .ui-widget-content .ui-state-default, .ui-widget-header .ui-state-default, .ui-button, html .ui-button.ui-state-disabled:hover, html .ui-button.ui-state-disabled:active {
        border: none;
        background: #ffc107;
        font-weight: normal;
        color: #454545;
    }
    .ui-slider-horizontal .ui-slider-handle {
        top: -1.2em;
    }
    .ui-slider .ui-slider-handle {
        width: 3em;
        height: 3em;
        cursor: pointer;
        border-radius: 30px;
        box-shadow: 0px 1px 1px 0px rgba(93, 64, 143, 0.5);
    }
}

