    padding: 50px 40px 0 40px;
    .red-star {
        &:after {
            left: 52px;
        }
        &:first-child {
            &:after {
                left: 47px;
            }
        }
    }
    .label {
        margin-bottom: 8px;
    }
    .default-btn {
        margin-top: 5px;
        margin-bottom: 29px;
    }
    .title-sm {
        font-size: 22px;
        margin: 0 0 12px 0;
        display: inline-block;
    }
    .social-list {
        margin-bottom: 26px;
        a {
            display: inline-block;
            width: 52px;
            height: 32px;
            background-color: #0073ad;
            text-align: center;
            border-radius: 5px;
            margin-right: 8px;
            opacity: 0.85;
            &:hover {
                opacity: 1;
            }
            &:last-child {
                margin-right: 0;
            }
            i {
                color: white;
                font-size: 24px;
                line-height: 30px;
            }
        }
        .fb {
            background-color: #5a79ab;
        }
        .g {
            background-color: #da4836;
        }
        .ok {
            background-color: #f69f1c;
        }
    }
    .def-txt {
        font-size: 11px;
        color: #727175;
        margin-bottom: 0;
    }
    .tabs {
        border-bottom: 1px solid #9575cd;
        margin-bottom: 40px;
        li {
            width: 49.5%;
            height: 40px;
            border-radius: 5px 5px 0 0;
            font-size: 20px;
        }
    }
    .box-reg {
        .default-btn {
            background-color: #673ab7;
            color: white;
            border: none;
            &:hover {
                border: none;
                background-color: #ffc107;
            }
        }
        .cond-label {
            a {
                color: #673ab7;
                &:hover {
                    color: #673ab7;
                }
            }
        }
    }
}
#basket_form {
    width: 792px;
    height: auto;
    text-align: center;
    padding: 19px 38px 0 38px;
    .title-sm {
        display: inline-block;
        margin: 0 0 14px 0;
        line-height: 23px;
        width: 100%;
        text-align: left;
    }
    .close-wp {
        margin-right: 8px;
    }
    .close-btn {
        display: inline-block;
        width: 24px;
        height: 24px;
        background: transparent;
        border: 1px solid #aca5b9;
        border-radius: 30px;
        text-align: center;
        padding: 0;
        color: #aca5b9;
        padding-left: 1px;
        margin: 24px 0 0 3px;
        i {
            font-size: 16px;
        }
        &:hover {
            color: #ff4d00;
            border: 1px solid #ff4d00;
        }
        &:focus {
            color: #ff4d00;
            border: 1px solid #ff4d00;
        }
    }
    .btn-wp {
        display: inline-block;
        width: auto;
        margin: 0 0 30px 0;
    }
    .default-btn {
        background-color: #673ab7;
        color: white;
        width: 242px;
        &:first-child {
            margin-right: 24px;
        }
        &:hover {
            color: #673ab7 !important;
            border: 1px solid #ffc107;
            background-color: #ffc107;
        }
        &:focus {
            color: #673ab7 !important;
            border: 1px solid #ffc107;
            background-color: #ffc107;
        }
    }
    .close2 {
        background-color: white;
        color: #673ab7;
        border: 1px solid #673ab7;
        &:hover {
            color: #673ab7 !important;
            border: 1px solid #ffc107;
            background-color: #ffc107;
        }
        &:focus {
            color: #ffc107 !important;
            border: 1px solid #ffc107;
            background-color: #ffc107;
        }
    }
    .total-price-wp {
        display: inline-block;
        width: auto;
        margin: 10px 0 0 0;
        .tov-old-price {
            margin-left: 3px;
        }
    }
    > div {
        display: inline-block;
    }
    .tov-photo-wp, .tov-name-wp {
        margin-right: 26px;
    }
    .tov-price-wp {
        margin-right: 8px;
    }
    .jq-number {
        padding: 0;
        display: block;
        width: 100%;
        margin-bottom: 0;
        margin-top: 23px;
    }
    .jq-number__field input {
        box-sizing: border-box;
        width: 100px;
        padding: 3px 55px 3px 3px;
        border: none;
        outline: none;
        background: none;
        font: 18px 'PT Sans', sans-serif;
        color: #333;
        text-align: center;
    }
    .jq-number__field {
        width: 100%;
        border: 1px solid #ddd8e6;
        border-bottom-color: #ddd8e6;
        border-radius: 0px;
        box-shadow: inset 1px 1px #F1F1F1, 0 1px 2px rgba(0,0,0,.1);
    }
    .jq-number__spin.minus {
        bottom: 4px;
        height: 9px;
    }
    .jq-number__spin.plus {
        top: 0px;
    }
    .jq-number__spin {
        width: 35px;
        height: 14px;
        overflow: hidden;
        border: none;
        border-bottom-color: white;
        border-radius: 0px;
        background: none;
        box-shadow: none;
        user-select: none;
    }
    .jq-number__spin:after {
        top: 6px;
        left: 8px;
        border-right: 8px solid transparent;
        border-bottom: 8px solid #673ab7;
        border-left: 8px solid transparent;
    }
    .jq-number__spin:hover {
        background: none;
    }
    .jq-number__spin.plus:hover:after {
        border-bottom-color: #ff4d00;
    }
    .jq-number__spin.minus:hover:after {
        border-top-color: #ff4d00;
    }
    .jq-number__spin.minus:after {
        top: 1px;
        border-top: 8px solid #673ab7;
        border-right: 8px solid transparent;
        border-left: 8px solid transparent;
    }
    .order-list {
        border: none!important;
        border-radius: 0px!important;
        padding: 0!important;
        margin-bottom: 0!important;
        &:hover {
            transform: scale(1)!important;
            box-shadow: none!important;
        }
        .order-inside-itm {
            padding: 20px 0;
            border-right: none;
            &:last-child {
                border-bottom: 1px solid #ddd8e6;
            }
            .tov-all-price-wp, .tov-all-price-wp {
                width: 117px;
            }
        }
    }
}

.check-wp {
    display: block;
    width: 100%;
    text-align: left;
}

.checkbox {
    vertical-align: top;
    margin: 0 3px 0 0;
    width: 17px;
    height: 17px;
    + label {
        cursor: pointer;
        font-weight: normal;
    }
    &:not(checked) {
        position: absolute;
        opacity: 0;
        margin: 18px 0 0 0;
        + label {
            position: relative;
            /* будем позиционировать псевдочекбокс относительно label */
            padding: 0 0 0 35px;
            /* оставляем слева от label место под псевдочекбокс */
            margin: 18px 0 0 0;
            line-height: 18px;
            word-spacing: 3px;
            font-size: 12px;
            a {
                color: #ff4d00;
                text-decoration: underline;
                &:hover {
                    color: #ff4d00;
                    text-decoration: none;
                }
            }
            &:before {
                content: '';
                position: absolute;
                top: 1px;
                left: 3px;
                width: 18px;
                height: 19px;
                border-radius: 5px;
                background: #CDD1DA;
                box-shadow: inset 0 2px 3px rgba(0, 0, 0, 0.2);
            }
            &:after {
                content: '\f00c';
                font-family: 'FontAwesome';
                color: #ff4d00;
                font-size: 11px;
                line-height: 19px;
                position: absolute;
                top: -3px;
                left: 2px;
                width: 18px;
                height: 19px;
                border-radius: 5px;
                background: #ffc107;
                box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
                transition: all .2s;
                visibility: hidden;
                opacity: 0;
                padding-left: 3px;
            }
        }
    }
    &:checked {
        + label {
            &:after {
                left: 3px;
                top: 1px;
                visibility: visible;
                opacity: 1;
            }
        }
    }
}

/*Modal styles*/
/*Block in content like modal*/
.call-form {
    width: 100%;
    height: auto;
    display: block;
    padding: 0;
    text-align: center;
    background-color: white;
    overflow: visible;
    border-radius: 0;
    .label {
        float: left;
        width: auto;
    }
    .red-star {
        &:after {
            right: -7px;
            left: inherit;
        }
    }
    .title-sm, .def-txt {
        text-align: left;
        display: inline-block;
    }
    .title-sm {
        margin: 0 0 23px 0;
        width: 100%;
    }
    .default-btn {
        background-color: #673ab7;
        color: white;
        &:hover {
            color: #ffc107!important;
            border: 1px solid #ffc107;
        }
    }
}
