/*Crumbs*/

.crumbs-list {
    margin-top: 21px;
    .crumbs-ul {
        margin: 0;
        padding: 0;
        list-style-type: none;
        li {
            display: inline-block;
            margin-right: 4px;
            font-size: 16px;
            &:last-child {
                margin-right: 10px;
            }
            a {
                display: inline-block;
                font-size: 16px;
                font-weight: normal;
                line-height: 16px;
                color: #544370;
                position: relative;
                padding-right: 15px;
                &::after {
                    content: '\e315';
                    font-family: 'Material Icons';
                    position: absolute;
                    font-size: 17px;
                    top: 2px;
                    right: -4px;
                    color: #9c7ed0;
                }
            }
        }
        li.active {
            color: #867a99;
        }
    }
}

